<template>
    <div class="fight">
        <p class="missionPoint">{{ $t("missionPoint") }}：{{ missionPoint }}</p>
        <div class="class">
            <!-- role -->
            <div class="row">
                <p class="classTitle">{{ $t("toRole") }}</p>
                <div class="toRole">
                    <p>
                        {{ $t("useMissionPoint") }}：
                        {{ baseConfig.fight.toRolePoint }}
                    </p>
                    <a-select
                        v-model:value="enemyId"
                        show-search
                        placeholder="Select a person"
                        :options="roleOptionsList"
                        :filter-option="filterOption"
                        @focus="handleFocus"
                        @blur="handleBlur"
                        @change="handleChange"
                    ></a-select>
                    <a-button
                        type="primary"
                        @click="toRole"
                        :disabled="!enemyId"
                    >
                        {{ $t("attack") }}
                    </a-button>
                </div>
            </div>
            <!-- monster -->
            <div class="row">
                <p class="classTitle">{{ $t("toMonster") }}</p>
                <!-- weapon -->
                <div class="toMonster">
                    <p>{{ $t("gainWeapon") }}</p>
                    <p>
                        {{ $t("useMissionPoint") }}：
                        {{ baseConfig.fight.toMonster }}
                    </p>
                    <a-button type="primary" @click="toMonster('2')">
                        {{ $t("attack") }}
                    </a-button>
                    <a-button type="primary" @click="toImitMonster">
                        {{ $t("imitAttack") }}
                    </a-button>
                    <div>
                        {{ $t("currentWinRate") }} {{ currentMonsterWinRate }}
                        <a-tooltip>
                            <template #title>{{ $t("monsterTips") }}</template>
                            <QuestionCircleOutlined style="color: #d2d2d2" />
                        </a-tooltip>
                    </div>
                </div>
                <!-- armor -->
                <div class="toMonster">
                    <p>{{ $t("gainArmor") }}</p>
                    <p>
                        {{ $t("useMissionPoint") }}：
                        {{ baseConfig.fight.toMonster }}
                    </p>
                    <a-button type="primary" @click="toMonster('3')">
                        {{ $t("attack") }}
                    </a-button>
                    <a-button type="primary" @click="toImitMonster">
                        {{ $t("imitAttack") }}
                    </a-button>
                    <div>
                        {{ $t("currentWinRate") }} {{ currentMonsterWinRate }}
                        <a-tooltip>
                            <template #title>{{ $t("monsterTips") }}</template>
                            <QuestionCircleOutlined style="color: #d2d2d2" />
                        </a-tooltip>
                    </div>
                </div>
                <!-- ornament -->
                <div class="toMonster">
                    <p>{{ $t("gainOrnament") }}</p>
                    <p>
                        {{ $t("useMissionPoint") }}：
                        {{ baseConfig.fight.toMonster }}
                    </p>
                    <a-button type="primary" @click="toMonster('4')">
                        {{ $t("attack") }}
                    </a-button>
                    <a-button type="primary" @click="toImitMonster">
                        {{ $t("imitAttack") }}
                    </a-button>
                    <div>
                        {{ $t("currentWinRate") }} {{ currentMonsterWinRate }}
                        <a-tooltip>
                            <template #title>{{ $t("monsterTips") }}</template>
                            <QuestionCircleOutlined style="color: #d2d2d2" />
                        </a-tooltip>
                    </div>
                </div>
            </div>
            <!-- boss -->
            <div class="row">
                <p class="classTitle">{{ $t("toBoss") }}</p>
                <div class="toBoss">
                    <p>
                        {{ $t("useMissionPoint") }}：{{
                            baseConfig.fight.toBoss
                        }}
                    </p>
                    <a-button type="primary" @click="toBoss">
                        {{ $t("attack") }}
                    </a-button>
                    <a-button type="primary" @click="toImitBoss">
                        {{ $t("imitAttack") }}
                    </a-button>
                    <div>
                        {{ $t("currentWinRate") }} {{ currentBossWinRate }}
                        <a-tooltip>
                            <template #title>{{ $t("bossTips") }}</template>
                            <QuestionCircleOutlined style="color: #d2d2d2" />
                        </a-tooltip>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <FightModal
        :modalVisible="modalVisible"
        :fightLog="fightLog"
        :spoils="spoils"
        @submit="modalClose"
    ></FightModal>
</template>

<script>
import { reactive, toRefs, onMounted, ref, createVNode, computed } from "vue";
import fightApi from "@/assets/api/fightApi";
import FightModal from "@/components/FightModal.vue";
import { Modal } from "ant-design-vue";
import { QuestionCircleOutlined } from "@ant-design/icons-vue";
import roleApi from "@/assets/api/roleApi";
import baseConfig from "@/assets/config/baseConfig.js";
import { transItemText, transItemType } from "@/assets/common/transItemText";
import { useI18n } from "vue-i18n";

export default {
    name: "Fight",
    components: { FightModal, QuestionCircleOutlined },
    // 战斗
    setup() {
        const { t } = useI18n();
        const state = reactive({
            uid: sessionStorage.getItem("uid"),
            missionPoint: "",
            enemyName: "",
            enemyId: "",

            roleOptionsList: [], // 人员列表

            fightLog: [], // 战斗日志
            spoils: [], // 战利品
            currentMonsterWinRate: "0%", // monster 胜率
            currentBossWinRate: "0%", // boss 胜率
            modalVisible: false, // 战斗日志弹窗
        });

        onMounted(async () => {
            // 获取所有角色列表
            await getRoleList();
            await toImitMonster();
            await toImitBoss();
        });

        // 获取所有角色列表
        const getRoleList = async () => {
            let res = await roleApi.getRoleList({});
            state.roleOptionsList = res.data
                .filter((i) => i.uid !== state.uid)
                .map((i) => {
                    return {
                        label: i.nickname,
                        value: i.uid,
                    };
                });
            state.missionPoint = res.data.find(
                (i) => i.uid === state.uid
            ).missionpoint;
        };

        // 获取角色信息
        const getRoleInfo = async () => {
            let res = await roleApi.getInfo({
                uid: state.uid,
            });
            state.missionPoint = res.data.baseInfo.missionpoint;
        };

        // attack to role
        const toRole = async () => {
            //  二次确认
            Modal.confirm({
                title: `${t("attackRoleTips")}：${state.enemyName}?`,
                content: createVNode("div", { style: "color:red;" }),
                okText: t("attack"),
                cancelText: t("cancel"),
                async onOk() {
                    let res = await fightApi.toRole({
                        uid: state.uid,
                        enemyId: String(state.enemyId),
                    });
                    state.fightLog = res.data.fightLog;
                    state.spoils = res.data.spoils;
                    state.modalVisible = true;

                    // 获取角色信息
                    await getRoleInfo();
                },
            });
        };

        const toMonster = async (type) => {
            //  二次确认
            Modal.confirm({
                title: `${t("attackMonsterTips")}：${t("toMonster")}?`,
                content: createVNode("div", { style: "color:red;" }),
                async onOk() {
                    let res = await fightApi.toMonster({
                        uid: state.uid,
                        type: type,
                    });
                    state.fightLog = res.data.fightLog;
                    state.spoils = res.data.spoils;
                    state.modalVisible = true;

                    // 获取角色信息
                    await getRoleInfo();
                },
            });
        };

        const toBoss = async () => {
            //  二次确认
            Modal.confirm({
                title: `${t("attackMonsterTips")}：${t("toBoss")}?`,
                content: createVNode("div", { style: "color:red;" }),
                async onOk() {
                    let res = await fightApi.toBoss({
                        uid: state.uid,
                    });
                    state.fightLog = res.data.fightLog;
                    state.spoils = res.data.spoils;
                    state.modalVisible = true;

                    // 获取角色信息
                    await getRoleInfo();
                },
            });
        };

        const toImitMonster = async () => {
            let res = await fightApi.toImitMonster({
                uid: state.uid,
            });
            let roleWin = 0;
            let roleName = JSON.parse(
                sessionStorage.getItem("userInfo")
            ).nickname;
            for (let i = 0; i < res.data.fightLogs.length; i++) {
                if (res.data.fightLogs[i].sub === roleName) {
                    roleWin++;
                }
            }
            state.currentMonsterWinRate = roleWin + "%";
        };

        const toImitBoss = async () => {
            let res = await fightApi.toImitBoss({
                uid: state.uid,
            });
            let roleWin = 0;
            let roleName = JSON.parse(
                sessionStorage.getItem("userInfo")
            ).nickname;
            for (let i = 0; i < res.data.fightLogs.length; i++) {
                if (res.data.fightLogs[i].sub === roleName) {
                    roleWin++;
                }
            }
            state.currentBossWinRate = roleWin + "%";
        };

        const handleChange = (value, role) => {
            // 记录选中对手的nickname
            state.enemyName = state.roleOptionsList.find(
                (i) => i.label === role.label
            ).label;
        };
        const handleBlur = () => {
            // console.log("blur");
        };
        const handleFocus = () => {
            // console.log("focus");
        };

        // 过滤 人员列表
        const filterOption = (input, option) => {
            return option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0;
        };

        // 关闭弹窗
        const modalClose = () => {
            state.modalVisible = false;
        };

        return {
            ...toRefs(state),
            baseConfig,
            filterOption,
            handleChange,
            handleBlur,
            handleFocus,
            QuestionCircleOutlined,
            toRole,
            toMonster,
            toImitMonster,
            toImitBoss,
            toBoss,
            modalClose,
        };
    },
};
</script>

<style lang="scss" scoped>
.fight {
    .missionPoint {
        margin: 10px;
    }

    .class {
        display: flex;
        flex-direction: column;

        .classTitle {
            margin: 10px;
        }

        .toRole,
        .toMonster,
        .toBoss {
            width: 250px;
            float: left;
            border: 1px solid #d2d2d2;
            border-radius: 8px;
            margin: 0 10px 10px;
            padding: 15px;
            display: flex;
            flex-direction: column;
            align-items: center;

            .ant-select,
            .ant-btn {
                width: 200px;
                margin-bottom: 10px;
            }
        }
    }
}
</style>
